@import "../../styles/variables";
@import "../../styles/mixins";

.wrapper {
  padding: 3rem 0;
  width: 100%;
  background-color: transparent;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
}

.title {
  margin: 0;
  @include h3;
}

.address {
  margin: 0;
  @include fontSmall;
}

.contacts {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  @include fontRegular;
  transition-duration: 0.3s;

  &:hover {
    color: $colorHighlightRed;
  }
}
