@import "../../styles/mixins";
@import "../../styles/variables";

.header {
  padding: 7rem 0;
}

.headerFixed {
  position: fixed;
  transform: translateY(-110%);
  transition-duration: 0s;
}

.headerFixedHidden {
  transform: translateY(-110%);
  transition-duration: 0.3s;
}

.headerFixedVisible {
  transform: translateY(0);
  transition-duration: 0.3s;
}

.wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navWrapper {
  margin-left: auto;
  overflow: hidden;
}

.nav {
  transform: translateX(100%);
  transition-duration: 0.3s;
}

.navOpened {
  transform: translateX(0);
}

.list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}

.item {
  &:not(:first-of-type) {
    margin-left: 3rem;
  }
}

.link {
  position: relative;
  display: block;
  @include fontNavigation;
  transition-duration: 0.3s;

  &::before {
    content: "";
    position: absolute;
    bottom: -0.2rem;
    left: -0.5rem;
    width: calc(100% + 0.5rem * 2);
    height: 1.2rem;
    background-color: $colorDarker;
    pointer-events: none;
    opacity: 0;
    transform: translateY(0.2rem);
    transition-duration: 0.3s;
    z-index: -1;
  }

  &:hover {
    color: red;

    &::before {
      opacity: 0.4;
      transform: translateY(-0.5rem);
    }
  }

  &:active {
    color: $colorLight;
  }
}

.burger {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 3rem;
  padding: 0;
  font-size: 0;
  width: 7rem;
  height: 7rem;
  background-color: transparent;
  border: none;
  box-shadow: none;
  cursor: pointer;

  //&::before {
  //  content: "";
  //  position: absolute;
  //  top: calc(4.5rem / 2);
  //  left: calc(50% - 3.5rem / 2);
  //  width: 3.5rem;
  //  height: 0.2rem;
  //  background-color: $colorDarker;
  //  box-shadow: 0 21px 0.1px 0 $colorDarker;
  //}
  //
  //&::after {
  //  content: "";
  //  position: absolute;
  //  top: calc(50% - 0.2rem / 2);
  //  left: calc(50% - 3.5rem / 2);
  //  width: calc(50% - 3.5rem / 2);
  //  height: 0.2rem;
  //  background-color: $colorDarker;
  //  transform:translateX(0);
  //  transition-duration: 0.3s;
  //}

  &:hover {
    .iconMiddle {
      transform: translateX(calc(50% - (3.5rem / 2) / 2 - (50% - 3.5rem / 2)));
    }

    .iconBottom {
      transform: translateX(0.3rem);
    }
  }
}

.icon {
  width: 3.5rem;
  height: 2.5rem;
  stroke: none;
  fill: $colorDarker;

  .iconMiddle,
  .iconBottom {
    transition-property: transform;
    transition-duration: 0.3s;
  }

  .iconClose {
    transform: rotate(45deg);
    transform-origin: 50% 50%;
  }
}

@media (max-width: 767px) {
  .header:not(.headerFixedVisible):not(.headerFixedHidden) {
    position: relative;
  }

  .nav {
    position: absolute;
    top: calc(100% + 1.5rem);
    right: 1rem;
    width: 30rem;
    background-color: $colorLightest;
    border-radius: 0.5rem;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
    opacity: 0;
    transform: scaleY(0);
    transform-origin: 50% 0;
    transition-duration: 0.3s;
  }

  .navOpened {
    padding: 2rem 0;
    opacity: 1;
    transform: scaleY(1);
  }

  .list {
    flex-direction: column;
  }

  .item {
    &:not(:first-of-type) {
      margin-left: 0;
    }
  }

  .link {
    padding: 1rem 2rem;

    &:hover {
      color: $colorLight;
      background-color: $colorLight;

      &::before {
        display: none;
      }
    }
  }

  .burger {
    position: relative;
    display: block;
    width: 4rem;
    height: 3rem;
    font-size: 0;
    background-color: transparent;
    box-shadow: none;
    border: none;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: $colorMain;
      box-shadow:
        0 10px 0 0 $colorMain,
        0 20px 0 0 $colorMain
    }
  }
}
