@import "../../styles/variables";
@import "../../styles/mixins";

.title {
  margin: 0;
  @include h2;
}

.wrapper {
  position: relative;
  margin-top: 3rem;

  :global {
    .swiper-wrapper {
      align-items: center;
    }
  }
}

.itemWrapper {
  display: flex;
  align-items: center;
  padding: 3rem;
  height: 12rem;
  background-color: $colorLightest;
}

.logo {
  filter: grayscale(1);
  transition-duration: 0.3s;
}

.item:hover {
  .logo {
    filter: grayscale(0);
  }
}

.controls {
  position: absolute;
  display: flex;
  width: 100%;
  z-index: 1;
}

.button {
  position: absolute;
  left: -5rem;
  margin: 0;
  padding: 0;
  font-size: 0;
  border: none;
  box-shadow: none;
}

.buttonNext {
  right: -5rem;
  margin-left: auto;
  transform: rotate(180deg);
}

.buttonDisabled {
  opacity: 0.5;
  pointer-events: none;
}

.logo {
  width: 100%;
}

.icon {
  fill: black;
}
